.Vendor-form-control:focus {
  color: #6a0c0c;
  background-color: #f4f2f2;
  border-color: #80bdff;
  outline: 0;
  box-shadow: 0 0 0 0.2rem rgba(229, 233, 238, 0.25);
}


.card-body1 {
  flex: 1 1 auto;
  min-height: 1px;
  /* padding: 1.25rem; */
  padding-left: 0.4em;
  padding-right: 0.4em;
  padding-top: 0em;
  padding-bottom: 0em;
  box-shadow: 0px -3px 3px -1px #acb7c1;
  border-radius: 10px;
}

.card-header1 {
  /* border: 1px solid #e5e8ea; */
  padding: 1.9em 0.8em;
  margin-bottom: 0.8em;
  box-shadow: 0px 5px 4px -2px #b0b3b7;
  border-radius: 10px;
}

.modal-content_docs .close {
  background-color: #0bb39b;
  border-radius: 50%;
  color: #fff;
  font-size: 15px;
  height: 24px;
  line-height: 6px;
  margin: 0;
  opacity: 1;
  padding: 0;
  position: absolute;
  right: 10px;
  top: 16px;
  width: 27px;
  z-index: 98;
}

.title_vendor {
  /* margin-right: 46px; */
  /* padding-top: 10px; */
  /* max-width: 25px; */
  padding: 0px;
  margin-bottom: 3px;
}

.custom_modal_size {
  max-width: 1120px;
}


.SubmitFooter .custombtn {
  margin-right: 16px;
}

.custom_danger_button {
  background: #d0455e;
  border: 1px solid #d24962;
}

