}

.Product_leftSide_fixed_content {
  height: 400px;
  overflow-y: auto;
}

.searchInputField .search {
  border-width: 3px;
  border-right-width: 1px;
  margin-bottom: 8px;
}

.control_search {
  border-color: #b8bec1 !important;
  box-shadow: none;
  font-size: 15px;
  height: 44px;
}

.has-search .form-control {
  padding-left: 2.375rem;
  max-width: 99%;
}

.has-search .form-control:focus {
  color: #110e0e;
  background-color: #fdfdfd;
  border-color: #80bdff;
  outline: 0;
  box-shadow: 0px 0px 4px 0.2rem rgba(9, 35, 68, 0.25);
}

.has-search .form-control-feedback {
  position: absolute;
  z-index: 2;
  display: block;
  width: 2.375rem;
  height: 2.375rem;
  line-height: 2.375rem;
  text-align: center;
  pointer-events: none;
  color: #aaa;
  padding-top: 4px;
}



