  /* max-height: 105px; */
  min-height: 152px;

}
.header {
  background: #667eea;
  background: -moz-linear-gradient(left, #667eea 0%, #764ba2 100%);
  background: -webkit-linear-gradient(left, #667eea 0%, #764ba2 100%);
  background: -ms-linear-gradient(left, #667eea 0%, #764ba2 100%);
  background: linear-gradient(90deg,#667eea 0,#764ba2);
  border-bottom: 1px solid transparent;
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.2);
  height: 60px;
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 1002;
}
/* login form */
body {
  background-color: #f8f9fa;
}
.signup-form {
  max-width: 400px;
  margin: 0 auto;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  background-color: #fff;
}
.signup-form{
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.15);
  transition: all .5s;
}
.signup-form:hover{
  box-shadow: 0 15px 15px rgba(0, 0, 0, 0.15);
  transform: translateY(-5px);
}
.signup-form h2 {
  text-align: center;
  margin-bottom: 30px;
}
.signup-form form {
  border-radius: 10px;
  background-color: #f7f7f7;
  padding: 30px;
  box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.1);
}
.signup-form  .form-group {
	margin-bottom: .5rem;
}
.login-form {
  margin-bottom: 5px !important;
}
.login-form {
  border-radius: 25px;
}
.custom-primary {
  border-radius: 25px;
  padding: 12px 30px;
  font-weight: bold;
}
.custom-primaryy:hover {
  background-color: #007bff;
}
.login-form:focus {
  border-color: #007bff;
  box-shadow: none;
}
