/* media design */
@media only screen and (max-width: 600px) {

  .publicheader {
    margin-top: .6rem;
    margin-left: 0.1rem;
    margin-right: .1rem;
  }
  .create_vendor_page_title {
    text-align: center;
  }
  .pdtitle{
    font-size: 1.25rem;
  }
  .pdnametitle{
    font-size: 1rem;
  }
  .bbimagelogo{
    width: 60px;
    height: 60px;
  }
  .bdimagelogo{
    width: 60px;
    height: 60px;
  }


  .displayflex {
    display: flex;
    text-align: center;
  }


  .card-body1 {
    flex: 1 1 auto;
    min-height: 1px;

    padding-left: 0.1em;
    padding-right: 0.1em;
    padding-top: 0em;
    padding-bottom: 0em;
  }

  .card-header1 {
    padding-bottom: 10px;
    padding-left: 5px;
  }

  .Button_Danger1 {
    padding: 0.575rem 1rem;
    font-size: .8rem;
    line-height: 1;

  }


  .Button_success {

    padding: 0.575rem 1rem;
    font-size: .8rem;
    line-height: 1;

    /* margin-left: 0.4em; */
    /* margin-right: 0.4em; */
  }


  .Button_primary {
    font-weight: 400;
    color: #fff;
    text-align: center;
    vertical-align: middle;
    border: 1px solid transparent;
    padding: 0.575rem 1rem;
    font-size: .6rem;
    line-height: 1;

  }

}

